import { Component, OnInit } from '@angular/core';
import { ICharacter } from '../../interfaces/character/character';
import { InfoAnim, EnumAnim } from '../../interfaces/animation/info-anim';
import { PlayerCharacter } from 'src/app/classes/player-character';
import { Character } from 'src/app/classes/character';

@Component({
  selector: 'app-ingame',
  templateUrl: './ingame.component.html',
  styleUrls: ['./ingame.component.scss']
})
export class IngameComponent implements OnInit {

  player: PlayerCharacter;
  enemies: Character[] = [];

  constructor() { }

  ngOnInit() {
    this.player = new PlayerCharacter(
      {
        name: "knight",
        sizeX: 384,
        sizeY: 192,
        columnN: 14,
        iddleAnim: {
          position: 0,
          length: 14,
          speed: 100,
          play: EnumAnim.loop
        },
        attackAnim: {
          position: 1,
          length: 14,
          speed: 80,
          play: EnumAnim.oneShoot
        },
        health: 100,
        coldDown: 2,
        damage: 50,
        height: 20
      }

    );

    this.enemies.push(
      new Character({
        name: "wolf",
        sizeX: 388,
        sizeY: 192,
        columnN: 15,
        iddleAnim: {
          position: 0,
          length: 12,
          speed: 100,
          play: EnumAnim.loop
        },
        attackAnim: {
          position: 1,
          length: 15,
          speed: 80,
          play: EnumAnim.oneShoot
        },
        health: 100,
        coldDown: 2.5,
        damage: 50,
        height: 20
      })
    );
    setTimeout(() => {
      this.enemies.push(
        new Character({
          name: "wolf",
          sizeX: 388,
          sizeY: 192,
          columnN: 15,
          iddleAnim: {
            position: 0,
            length: 12,
            speed: 100,
            play: EnumAnim.loop
          },
          attackAnim: {
            position: 1,
            length: 15,
            speed: 80,
            play: EnumAnim.oneShoot
          },
          health: 100,
          coldDown: 2.5,
          damage: 50,
          height: 30
        })
      );
    }, 5000)

  }

  gridMonster(index: number) {
    return "grid-monster-" + this.enemies.length + "-" + index;
  }

}
